.parent{
    background-color: #8ac6e8;
    display: flex;
    width: 60%;
    height: 448px;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 15px;
}

.inputs{
    height: 70%;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center; 
    flex-wrap: wrap;
}

.secc{
    color: #1f7f12;
    width: 50%;
    background-color: #a9f2ba;
    height: 10%;
    display: flex;
    font-size: 20px;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
    font-weight: bold;
    border-radius: 15px;
}

.err-cont{
    display: flex;
    flex-direction: column;
    width: 40%;
}

.input{
    width: 100%;
    height: 30px;
    border-radius: 5px;
    border: 0.5px solid #1c1414;
}

.inputs span{
    color: red;
    font-family: monospace;
    font-size: 15px;
    margin-top: 5px;
}

.select-box{
    height: 30px;
    width: 100%;
    border-radius: 5px;
}

.btn{
    width: 80%;
    display: flex;
    justify-content: center;
}

.btn button{
    width: 23%;
    height: 32px;
    border-radius: 10px;
    border: 1px solid black;
    background-color: transparent;
    transition: 200ms;
    cursor: pointer;
}

.btn button:hover{
    border: none;
    background-color: #a9f2ba;
}